export default {
	name:'async-switch',
	props:{
		checked: {
			type: Boolean,
			default: false
		},
		disabled:{
			type: Boolean,
			default: false
		},
		color:{
			type:String,
			default:'#6E0012'
		},
		disabledColor:{
			type:String,
			default:'#e5e5e5'
		}
	},
	data() {
		return {
			
		};
	},
	computed:{
		changeColor(){
			return (this.disabled ? this.disabledColor : this.color)
		}
	},
	methods:{
		onChange(){
			/* 禁用状态不返回事件 */
			if(this.disabled){
				return;
			}
			this.$emit("change");
		}
	}
}
